.file-node {
  cursor: pointer;
  width: fit-content;
}

.file-node .folder,
.file-node .file {
  padding: 4px;
}

.file-node .folder {
  color: blue;
}

.file-node .file {
  color: black;
}

.level-1 {
  margin-left: 20px;
}

.level-2 {
  margin-left: 40px;
}

.level-3 {
  margin-left: 60px;
}

.file-content {
  width: fit-content;
}
.header {
  text-align: center;
  background-color: darkblue;
  color: white;
  font-size: 24px;
   p {
    margin: 0;
   }
}
.body-content {
  display: flex;
  width: 100%;
}
.sidebar {
  background: gray;
  max-width: 300px;
  width: 300px;
  overflow: auto;
}
.file-content-body {
  flex: 1 0 auto;
  display: flex;
  justify-content: center;
  background: lightgray;
}

.main {
  width: 80%;
}